import React from "react";
import { InputField } from "./../../../components/form-control/InputField/index";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { Button, LinearProgress, Divider } from "@mui/material";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import { Avatar } from "@mui/material";
import Typography from "@mui/material/Typography";
import { Container } from "@mui/system";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import styled from "@emotion/styled";
import { PasswordField } from "../../../components/form-control/PasswordField";

const schema = yup
  .object({
    username: yup
      .string()
      .required("Vui lòng nhập tên tài khoản")
      .min(5, "Tên tài khoản phải nhiều hơn 5 kí tự"),
    password: yup.string().required("Vui lòng nhập mật khẩu"),
  })
  .required();

const RegisterTitle = styled(Typography)({
  textAlign: "center",
  fontWeight: "bold",
  color: "#e7880d",
});

const ContainerLogin = styled(Container)({
  border: "1px solid #ccc",
  borderRadius: "12px",
  boxShadow: "1px 1px 2px #ccc",
  background: "#fff"
});

export const LoginForm = (props) => {
  const form = useForm({
    defaultValues: {
      username: "",
      password: "",
    },
    resolver: yupResolver(schema),
  });

  const handleFormSubmit = async (values) => {
    const { onSubmit } = props;

    if (onSubmit) {
      await onSubmit(values);
    }
  };

  const { isSubmitting } = form.formState;

  return (
    <ContainerLogin maxWidth="xs">
      <CssBaseline />

      {isSubmitting && <LinearProgress />}
      <Box style={{ textAlign: "center", marginTop: '12px' }}>
        <img
          src="/cong-an-hieu.png"
          alt="logo"
          width="80"
          style={{ margin: "0 auto" }}
        />
      </Box>
      <RegisterTitle component="h4">
        Đăng nhập quản trị thư viện số
        <br /> Công an Hưng Yên
      </RegisterTitle>

      <form onSubmit={form.handleSubmit(handleFormSubmit)}>
        <InputField
          name="username"
          label="Username"
          form={form}
          disabled={false}
        />

        <PasswordField
          name="password"
          label="Password"
          form={form}
          disabled={false}
        />

        <Button
          type="submit"
          variant="contained"
          fullWidth
          style={{ marginTop: "24px" }}
          disabled={isSubmitting}
        >
          Đăng nhập
        </Button>

        <Box mt={2} mb={2}>
          <Typography variant="body1" style={{ textAlign: "center" }}>
            Thiết kế phần mềm:{" "}
            <span style={{ fontWeight: "bold" }}>Vũ Văn Tính</span>
          </Typography>
        </Box>
      </form>
    </ContainerLogin>
  );
};
