import { Box, styled } from '@mui/material';
import React from 'react'
import { loginAccount } from '../authSlice';
import { LoginForm } from '../components/LoginForm';
import { useDispatch } from 'react-redux';
import { unwrapResult } from '@reduxjs/toolkit';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import Cookies from 'js-cookie'


const BoxLogin = styled(Box)({
    display: "flex",
    alignItems: "center",
    height: "100vh",
    margin: "0 12px",
    backgroundImage: "url(/thuvienso.jpg)",
    backgroundRepeat: "none",
    backgroundSize: "cover"
})

const Login = () => {

  const navigate = useNavigate()

    const dispatch = useDispatch();
    const {enqueueSnackbar} = useSnackbar();


    const handleLogin = async (values) => {
        const action = loginAccount(values);
        try {
            const resultAction = await dispatch(action);
            const data = unwrapResult(resultAction)
            enqueueSnackbar('Đăng nhập tài khoản thành công!',{
                anchorOrigin: {
                  vertical: 'bottom',
                  horizontal: 'right'
                },
                 variant: 'success' 
              });
              
              Cookies.remove('refreshToken')
              Cookies.set("refreshToken", data.refreshToken,{ expires: 7, secure: true, sameSite: "none" })
              navigate('/admin/tai-khoan')

        } catch (error) {
            enqueueSnackbar(error.message,{
                anchorOrigin: {
                  vertical: 'bottom',
                  horizontal: 'right'
                },
                 variant: 'error' 
              });
        }
    };


  return (
    <BoxLogin>
        <LoginForm onSubmit={handleLogin}/>
    </BoxLogin>
  )
}


export default Login;